import modalComponent from '@/components/modalComponent.vue'
import btnComponent from '@/components/btnComponent.vue'
import listItemComponent from '@/components/listItemComponent.vue'
import badgeComponent from '@/components/badgeComponent.vue'
import countToComponent from '@/components/countToComponent.vue'
export default {
	components: {
		modalComponent,
		btnComponent,
		listItemComponent,
		badgeComponent,
		countToComponent,
	},
	data() {
		return {
			$app: this.$app,
			showLoading: true,
			requestCount: 7,
			tips: false,

			userCurrency: {},

			star: {},
			userRankList: {
				thisday_count: [],
				thisweek_count: [],
			},
			chartList: [],
			chartIndex: -1, // 
			modal: '', // 
			chartMsg: '', // 
			sendCount: '', // 
			starRankList: [],
			invitAward: {}, // 
			invitList: [], // 
			invitNum: 10, // 
			hasEarnCount: 0,
			invitListPage: 1,
			hasInvitcount: 0,
			spriteEarn: false,
			rechargeList: [], // 
			danmaku: null, // 
			article: {}, // 

			fakeinvitList: [],
			invitFakePage: 1,
			fatherEarn: 0,

			mass: {}, // 

			activeInfo: null,
			current: 0,

			itemList: [],

			giftItemList: [],
			chartAni: '',

			// ,
			currentUser: {},
			sendOtherNum: 1,
			sendOtherType: '', // 

			sprite_level: 0,
			friendTotal: 0,
			captain: 0, // 

			siginList: [],
			signin_day: 1,
			signin_coin: 0,

			disLeastCount: 0, // 
			myTotalCount: 0, // 

			sideBtnOpacity: 1, // 
			urgeSendTips: '', // tips

			inputing: false, // 

			send_num_list: [99, 520, 1314, 9999, 66666, ''],

			myRank: {},

			sendDanmaku: [
				[],
				[],
				[]
			], // 
			danmakuClosed: this.$app.getData('danmakuClosed'),

			// 
			sendFansboxNum: '', // 
			sendFansBoxUseCurrency: 0, // 0  1
			sendFansBoxCoinNum: 0, // 
			sendFansboxPeople: 1, // 
			sendFansBoxList: [], // 
			sendFansNoSettle: 0, // 
			sendFansNoticeId: 0, // id

			// 
			newboyList: [],
			newboyBg: 'https://mmbiz.qpic.cn/mmbiz_png/w5pLFvdua9Ep3RhxrWX9ibdRVKkjMQibIDNy4g4CU7wJTicw6PiaPhZmIsxMhu2kkD5iaDgmdFpDyg5yZYwJwibYXAUw/0',
			newboyCurrent: 0,
			newboyRemainTime: '',

			// 
			signGift_currentCategory: 0,
			signGift_categoryList: [],
			signGift_list: [],
		};
	},
	created() {
		this.sendDanmakuIndex = 0
		this.initDanmaku()
	},
	destroyed() {
		clearInterval(this.timeId_danmaku)
	},
	methods: {
		/**
		 * 
		 */
		load(starid) {
			this.current = 0
			this.star.id = starid

			// 
			this.loadData()


			this.userCurrency = this.$app.getData('userCurrency')
		},
		unLoad() {
			this.leaveGroup()
		},
		loadData() {
			const clientId = this.$app.getData('clientId')
			if (!clientId) {
				this.$app.invokeSocket()
				setTimeout(() => {
					this.loadData()
				}, 1500)
			} else {
				this.$app.request('page/group', {
					starid: this.star.id,
					client_id: clientId,
				}, res => {
					// 
					const star = res.data.starInfo
					uni.setNavigationBarTitle({
						title: star.name
					})
					this.star = {
						id: star.id,
						avatar: star.head_img_s ? star.head_img_s : star.head_img_l,
						name: star.name,
						weekHot: this.$app.formatNumber(star.star_rank.week_hot),
						weekRank: star.star_rank.week_hot_rank,
						share_img: star.share_img,
						qrcode: star.qrcode,
						isBirth: star.isBirth || false,
					}
					// 
					const chartList = []
					res.data.chartList.forEach((e, i) => {
						const item = {
							uid: e.user && e.user.id,
							avatar: e.user && e.user.avatarurl || this.$app.AVATAR,
							nickname: e.user && e.user.nickname || this.$app.NICKNAME,
							content: e.content,
							// 
							captain: e.user && e.user.user_star && e.user.user_star.captain || 0,
							// 
							leader: e.user && e.user.isLeader,
							level: e.user && e.user.level,
							badgeId: e.user && e.user.user_ext && e.user.user_ext.badge_id,
							sendtime: e.create_time.slice(11),
							headwear: e.user && e.user.headwear && e.user.headwear.img
						}

						chartList.push(item)
					})
					this.disLeastCount = res.data.disLeastCount
					this.chartList = chartList
					this.$nextTick(function() {
						this.chartIndex = this.chartList.length - 1
					})
					// 
					setTimeout(() => {
						this.captain = res.data.captain
						// 
						const userRankList = []
						res.data.userRank.forEach((e, i) => {
							userRankList.push({
								avatar: e.user && e.user.avatarurl || this.$app.AVATAR,
								nickname: e.user && e.user.nickname || this.$app.NICKNAME,
								hot: e.thisweek_count,
							})
						})
						this.userRankList['thisday_count'] = userRankList

						// 
						this.article.name = res.data.article.name
						this.article.id = res.data.article.id

					}, 300)

					if (this.$app.getData('config').fansbox_open == '1') {
						this.$app.request('fans/mybox', {}, res => {
							this.sendFansBoxList = res.data.list
							this.sendFansNoSettle = res.data.can_settle
							this.isJoinFanclub = res.data.isJoinFanclub
						})
					}
				})
			}

		},
		// 
		danmakuSwitch(e) {
			this.danmakuClosed = !e.detail.value
			this.$app.setData('danmakuClosed', !e.detail.value)
		},
		openUserModal() {
			this.modal = 'user'
		},
		// 
		forbidden() {
			this.$app.modal(`${this.currentUser.nickname}`, () => {
				this.$app.request('user/forbidden', {
					user_id: this.currentUser.id,
				}, res => {
					this.$app.toast('', 'success')
					this.modal = ''
				}, 'POST', true)
			})
		},

		sendItemOther(item, num, index) {
			this.$app.modal(`${num}${item.name}${this.currentUser.nickname}`, () => {
				this.$app.request('user/sendItemToOther', {
					user_id: this.currentUser.id,
					item_id: item.id,
					num: num
				}, res => {
					this.$app.toast('', 'success')
					this.modal = ''
					this.itemList[index].self -= num
				}, 'POST', true)
			})
		},

		copy(text) {
			this.$app.copy(text)
		},

		// 
		sendOther() {
			let sendOtherNum = parseInt(this.sendOtherNum)
			this.sendOtherNum = 1

			if (sendOtherNum <= 0) {
				this.$app.toast('')
				return
			}
			if (this.sendOtherType == 'flower') {
				var text = ''
			} else if (this.sendOtherType == 'stone') {
				var text = ''
			}
			if (sendOtherNum > this.userCurrency[this.sendOtherType]) {
				this.$app.toast('' + text)
				return
			}

			this.$app.modal(`${sendOtherNum}${text}${this.currentUser.nickname}`, () => {
				this.$app.request('user/sendToOther', {
					user_id: this.currentUser.id,
					num: sendOtherNum,
					type: this.sendOtherType,
				}, res => {
					this.$app.toast('', 'success')
					this.modal = ''
					this.$app.request(this.$app.API.USER_CURRENCY, {}, res => {
						this.$app.setData('userCurrency', res.data)
						this.userCurrency = this.$app.getData('userCurrency')
					})
				}, 'POST', true)
			})
		},
		// 
		deleteFriend(item, index) {
			this.$app.modal('' + item.nickname + '', () => {
				this.$app.request('user/delFriend', {
					user_id: item.uid
				}, res => {
					this.invitList.splice(index, 1)
				}, 'POST', true)
			})
		},
		// 
		addFanclub(id) {
			this.$app.request('fans/join', {
				id: id
			}, res => {
				this.$app.toast('', 'success')
				this.modal = ''
			}, 'POST', true)
		},
		// 
		tapUser(uid) {
			if (uid == this.$app.getData('userInfo').id) return
			this.currentUser = {}
			this.modal = 'userInfo'
			this.$app.request('user/info', {
				user_id: uid
			}, res => {
				this.currentUser = res.data
			})
		},

		goPageHasStar(url) {
			if (this.$app.getData('userStar').id) {
				this.$app.goPage(url)
			} else {
				this.$app.toast('')
			}
		},

		like(user_id, index) {
			this.$app.request('user/like', {
				user_id
			}, res => {
				if (index == -1) {
					this.myRank.like_count++
				} else {
					this.userRankList['thisweek_count'][index].like_count++
				}

			}, 'POST', true)
		},

		getActiveInfo() {
			this.$app.request(this.$app.API.EXT_ACTIVEINFO, {
				starid: this.star.id
			}, res => {
				this.activeInfo = res.data
			})
		},

		getLocalImg(src, callback) {
			if (~src.indexOf('http')) {
				uni.getImageInfo({
					src,
					success: function(res) {
						callback && callback(res.path)
					}
				})
			} else {
				callback && callback(src)
			}
		},
		getShareText(type) {
			this.$app.request(this.$app.API.EXT_SHARETEXT, {
				type,
			}, res => {
				this.modal = ''
				this.$app.copy(res.data.share_text)
			})
		},
		// canvas
		drawCanvas() {
			if (this.star.id != this.$app.getData('userStar').id) return
			this.modal = 'canvas'
			var rate = this.$app.getData('sysInfo').windowWidth / 375 / 2.0
			var ctx = uni.createCanvasContext('mycanvas', this);
			// 
			const drawText = function() {
				ctx.setFillStyle('#FFFFFF') //

				ctx.setFontSize(18) //10
				ctx.setTextAlign('center')
				ctx.fillText(this.$app.getData('config').canvas_title[0], 240 * rate, 200 * rate) //
				ctx.fillText(this.$app.getData('config').canvas_title[1], 240 * rate, 250 * rate) //

				ctx.fillText(this.star.name, 140 * rate, 636 * rate) //

				ctx.setFontSize(8) //10
				ctx.setTextAlign('left')
				ctx.fillText(`:NO.${this.star.weekRank}`, 265 * rate, 616 * rate) //
				ctx.fillText(`:${this.star.weekHot}`, 265 * rate, 640 * rate) //

				ctx.fillText(`${this.$app.getData('userInfo').nickname}`, 130 * rate, 774 * rate) //
				ctx.fillText(`${this.star.name}`, 130 * rate, 804 * rate) //
			}.bind(this)

			// 
			// 
			uni.showLoading({
				title: ""
			})
			this.getLocalImg('/static/image/canvas-bg.png', src => {
				ctx.drawImage(src, 0, 0, 480 * rate, 854 * rate);
				//  
				this.getLocalImg(this.star.share_img || this.star.avatar, src => {
					ctx.drawImage(src, 48 * rate, 286 * rate, 382 * rate, 305 * rate);
					// 
					this.getLocalImg(this.$app.getData('userInfo').avatarurl || this.$app.AVATAR, src => {
						ctx.save() //
						ctx.beginPath() //
						ctx.arc(79 * rate, 784 * rate, 40 * rate, 0, 2 * Math.PI, false) //
						ctx.clip() //
						ctx.drawImage(src, 38 * rate, 744 * rate, 80 * rate, 80 * rate) //
						ctx.restore() //
						// 
						this.getLocalImg(this.$app.getData('qrcode') || this.$app.QRCODE, src => {
							ctx.save() //
							ctx.beginPath() //
							ctx.arc(400 * rate, 780 * rate, 50 * rate, 0, 2 * Math.PI, false) //
							ctx.clip() //
							ctx.drawImage(src, 350 * rate, 730 * rate, 100 * rate, 100 * rate);
							ctx.restore() //

							// 
							drawText()
							// 
							ctx.draw(false, () => {
								// 
								uni.canvasToTempFilePath({
									canvasId: 'mycanvas',
									success: res => {
										this.canvasImg = res.tempFilePath
										console.log(this.canvasImg);

									}
								}, this);
							})


							uni.hideLoading()
						})
					})
				})
			})
		},
		//
		saveCanvas() {
			// 
			uni.saveImageToPhotosAlbum({
				filePath: this.canvasImg,
				success: () => {
					this.$app.toast('', 'success')
				}
			});
		},
		/**
		 * 
		 * 
		 */
		addChartMsg(data) {
			const item = {
				uid: data.user_id,
				avatar: data.user && data.user.avatarurl || this.$app.AVATAR,
				nickname: data.user && data.user.nickname || this.$app.NICKNAME,
				content: data.content,
				// 
				captain: data.user && data.user.user_star && data.user.user_star.captain || 0,
				// 
				leader: data.user && data.user.isLeader,
				level: data.user && data.user.level,
				badgeId: data.user && data.user.user_ext && data.user.user_ext.badge_id,
				sendtime: data.create_time.slice(11),
				headwear: data.user && data.user.headwear && data.user.headwear.img
			}

			this.chartList.push(item)

			this.$nextTick(function() {
				this.chartIndex = this.chartList.length - 1
			})
		},
		// 
		initDanmaku() {
			clearInterval(this.timeId_danmaku)
			this.danmaku = this.$app.danmakuQueue.shift() || null
			this.timeId_danmaku = setInterval(() => {
				this.danmaku = this.$app.danmakuQueue.shift() || null
			}, 10000)
		},

		// 
		sayworld() {
			if (!this.chartMsg.trim()) return
			if (this.chartMsg.trim().length > 16) {
				this.$app.toast('')
				return
			}
			this.modal = ''
			this.$app.request(this.$app.API.USER_SAYWORLD, {
				content: this.chartMsg.trim()
			}, res => {
				this.chartMsg = ''
				this.$app.request(this.$app.API.USER_CURRENCY, {}, res => {
					if (this.$app.getData('userCurrency')['trumpet'] == res.data.trumpet) {
						this.$app.toast('')
					} else {
						this.$app.toast('', 'success')
					}
					this.$app.setData('userCurrency', res.data)
					this.userCurrency = this.$app.getData('userCurrency')
				})

			}, 'POST', true)
		},
		/**
		 * 
		 */
		getUserInfo(e) {
			const userInfo = e.detail.userInfo
			if (userInfo) {
				this.$app.request(this.$app.API.USER_SAVEINFO, {
					iv: e.detail.iv,
					encryptedData: e.detail.encryptedData,
				}, res => {
					this.$app.request(this.$app.API.USER_INFO, {
						user_id: this.$app.getData('userInfo').id
					}, res => {
						this.$app.setData('userInfo', res.data, true)
						this.sendOrFollow()
					})
				}, 'POST', true)
			}

		},
		changeSignGift(index) {
			this.signGift_currentCategory = index
			this.getsignGift()
		},
		newboyOpen() {
			this.modal = 'newboy'
			this.$app.request('task/taskgiftCategory', {}, res => {
				this.signGift_categoryList = res.data
				this.getsignGift()
			})
		},
		getsignGift() {
			this.$app.request('task/taskGift', {
				cid: this.signGift_categoryList[this.signGift_currentCategory].id
			}, res => {
				this.signGift_list = res.data.list
			})
		},
		signGiftSettle() {
			this.$app.request('task/taskGiftSettle', {
				cid: this.signGift_categoryList[this.signGift_currentCategory].id
			}, res => {
				this.$app.toast('', 'success')
				this.getsignGift()
			}, 'POST', true)
		},

		sendFansChange() {
			uni.showActionSheet({
				itemList: ['', ''],
				success: res => {
					this.sendFansBoxUseCurrency = res.tapIndex
					this.sendFansBoxCoinNum = 0
				}
			})
		},

		sendFansInput(e) {
			this.sendFansboxNum = e.detail.value
			if (this.sendFansBoxUseCurrency == 0) {
				//  // 1 = 3000
				this.sendFansBoxCoinNum = this.sendFansboxNum * 3000
			} else if (this.sendFansBoxUseCurrency == 1) {
				//  // 1 = 300
				this.sendFansBoxCoinNum = this.sendFansboxNum * 300
			} else if (this.sendFansBoxUseCurrency == 2) {
				//  // 1 = 1
				this.sendFansBoxCoinNum = this.sendFansboxNum * 1
			}
		},
		sendFansBoxBtn() {
			if (this.sendFansBoxCoinNum < 300000) {
				this.$app.toast('30')
				return
			}

			if (this.sendFansboxPeople < 10 || this.sendFansboxPeople > 100) {
				this.$app.toast('10-100')
				return
			}

			this.$app.request('fans/sendbox', {
				type: this.sendFansBoxUseCurrency,
				consume: this.sendFansboxNum,
				people: this.sendFansboxPeople,
			}, res => {
				this.modal = ''
				this.$app.toast('', 'success')
			}, 'POST', true)
		},
		fansBoxOpen() {
			// if (this.isJoinFanclub) {
			this.modal = 'fansBox'
			this.$app.request('fans/mybox', {}, res => {
				this.sendFansBoxList = res.data.list
				this.sendFansNoSettle = res.data.can_settle
				this.sendFansNoticeId = res.data.noticeId
			})
			// } else {
			// 	this.$app.modal(``, () => {
			// 		this.goPageHasStar('/pages/fans/fans_list')
			// 	}, '')
			// }

		},
		goFansBox(id) {
			this.$app.goPage(`/pages/lottery/box_open?id=${id}`)
		},
		/***/
		steal(starid, index, steal) {
			if (steal > 0) return
			this.$app.request(this.$app.API.STAR_STEAL, {
				starid,
				index
			}, res => {
				this.starRankList[index].steal = 60
				this.$app.toast(`${res.data.count}`)

				this.getStarRank()
				this.$app.request(this.$app.API.USER_CURRENCY, {}, res => {
					this.$app.setData('userCurrency', res.data)
				})
			}, 'POST', true)
		},
		getInvitAward(uid, status, index) {
			if (status != 1) return
			this.$app.request(this.$app.API.USER_INVITAWARD, {
				ral_user_id: uid
			}, res => {
				this.fakeinvitList[index].status = 2
				this.$app.toast('')
				this.$app.request(this.$app.API.USER_CURRENCY, {}, res => {
					this.$app.setData('userCurrency', res.data)
				})
			}, 'POST', true)
		},
		goOther(item) {
			this.modal = ''
			this.$app.goPage('/pages/subPages/pet/other/other?user_id=' + item.uid + '&off=' + item.off)
		},
		// 
		getFakeInviteList() {
			this.$app.request(this.$app.API.USER_INVITLIST, {
				page: this.invitFakePage
			}, res => {
				this.invitAward = res.data.award
				this.hasInvitcount = res.data.hasInvitcount
				// 
				this.hasEarnCount = res.data.list.hasEarnCount

				const resList = []
				res.data.list.list.forEach((e, i) => {
					resList.push({
						avatar: e.user && e.user.avatarurl || this.$app.AVATAR,
						nickname: e.user && e.user.nickname || this.$app.NICKNAME,
						status: e.status,
						uid: e.ral_user_id
					})
				})

				if (this.invitFakePage == 1) {
					this.fakeinvitList = resList
				} else {
					this.fakeinvitList = this.fakeinvitList.concat(resList)
				}

				this.$app.closeLoading(this)
			})
		},
		// 
		getInvitList() {
			this.$app.request(this.$app.API.USER_INVITLIST, {
				type: 1,
				page: this.invitListPage || 1
			}, res => {
				this.invitAward = res.data.award
				const resList = []
				this.spriteEarn = false
				this.friendTotal = res.data.list.total_count
				res.data.list.list.forEach((e, i) => {
					resList.push({
						avatar: e.user && e.user.avatarurl || this.$app.AVATAR,
						status: e.status,
						uid: e.user && e.user.id || 0,
						nickname: e.user && e.user.nickname || this.$app.NICKNAME,
						earn: e.sprite.earn,
						off: e.off,
					})

					if (e.sprite.earn >= 100) {
						// 
						this.spriteEarn = true
					}
				})
				if (this.invitListPage == 1) {
					this.invitList = resList
				} else {
					this.invitList = this.invitList.concat(resList)
				}

				this.$app.closeLoading(this)
			})
		},
		setSendCount(e) {
			this.sendCount = e.detail.value
		},
		/**
		 * 
		 */
		sendHot(count) {
			if (count) {
				if (count == '') {
					if (this.current == 0) {
						// 
						this.sendCount = this.userCurrency.coin
					} else if (this.current == 1) {
						// 
						this.sendCount = this.userCurrency.flower
					} else if (this.current == 2) {
						// 
						this.sendCount = this.userCurrency.old_coin
					}
					return
				} else {
					this.sendCount = count
				}
			}
			if (!parseInt(this.sendCount)) {
				this.$app.toast('')
				return
			}

			this.$app.request(this.$app.API.STAR_SENDHOT, {
				starid: this.star.id,
				hot: parseInt(this.sendCount),
				type: this.current + 1,
				danmaku: Number(!this.danmakuClosed),
			}, res => {
				this.modal = ''
				this.getStarInfo()
				this.userRankPage = 1
				this.getUserRank('thisday_count')
				if (this.star.id == this.$app.getData('userStar').id) {
					// 
					this.getUserRank()
					this.modal = 'sendOver'
				}

				this.$app.request(this.$app.API.USER_CURRENCY, {}, res => {
					this.$app.setData('userCurrency', res.data)
					this.userCurrency = res.data
				})

				this.$app.toast('', 'success')

			}, 'POST', true)
		},
		/**
		 * 
		 */
		sendMsg() {
			const chartMsg = this.chartMsg.trim()
			if (chartMsg.length > 32) {
				this.$app.toast('')
				return
			}
			this.chartMsg = ''
			if (!chartMsg) return

			this.modal = ''
			this.$app.invokeSocket()
			this.$app.request(this.$app.API.STAR_SENDMSG, {
				starid: this.star.id,
				content: chartMsg,
			})
		},
		/**
		 * 
		 */
		sendOrFollow(e) {
			// if (!this.$app.getData('userInfo').nickname) {
			// 	this.$app.toast('')
			// 	return
			// }
			if (this.$app.getData('userStar')['id']) {
				// 
				if (this.$app.getData('userStar')['id'] == this.star.id ||
					this.star.isBirth) {
					// 
					this.sendCount = ''
					this.modal = 'send'
					// formId
					const formId = e && e.detail && e.detail.formId || null

					if (formId && formId != 'the formId is a mock one') {
						this.$app.request(this.$app.API.EXT_SAVEFORMID, {
							formId
						})
					}

					if (this.star.isBirth) {
						this.current = 1
					}

					return
				} else {
					if (this.$app.getData('userInfo').type != 1) {
						// 
						this.$app.toast(``)
						return
					}
				}

			}
			// 
			this.$app.modal(`\n${this.star.name}`, () => {
				this.$app.request(this.$app.API.STAR_FOLLOW, {
					starid: this.star.id,
					rer_user_id: this.$app.getData('referrer'), // 
				}, res => {
					if (this.$app.getData('userInfo').type == 1) {
						// 
						this.$app.token = ''
						this.$app.request(this.$app.API.USER_INFO, {}, res => {
							this.$app.setData('userInfo', res.data, true)
							this.$app.request(this.$app.API.USER_CURRENCY, {}, res => {
								this.$app.setData('userCurrency', res.data)
							})
							this.$app.request(this.$app.API.USER_STAR, {}, res => {
								this.$app.setData('userStar', res.data, true)
								this.$app.goPage('/pages/group/group?tips=1')
							})
						})
					} else {
						this.$app.request(this.$app.API.USER_STAR, {}, res => {
							this.$app.setData('userStar', res.data)
							this.$app.setData('noob', true)
							this.chartMsg = `${this.$app.getData('userInfo').nickname}~`
							this.sendMsg()
							this.$app.toast('')
							setTimeout(() => {
								this.$app.goPage('/pages/group/group?tips=1')
							}, 200)
						})
					}

				}, 'POST', true)
			})

		},
		getStarInfo() {
			this.$app.request(this.$app.API.STAR_INFO, {
				starid: this.star.id
			}, res => {
				const star = res.data

				this.star = {
					id: star.id,
					avatar: star.head_img_s ? star.head_img_s : star.head_img_l,
					name: star.name,
					weekHot: this.$app.formatNumber(star.star_rank.week_hot),
					weekRank: star.star_rank.week_hot_rank,
					share_img: star.share_img, // 
					qrcode: star.qrcode,
					isBirth: star.isBirth || false,
				}
			})
		},
		getArticle() {
			this.$app.request(this.$app.API.ARTICLE, {}, res => {
				this.article.name = res.data.name
				this.article.id = res.data.id
				this.$app.closeLoading(this)
			})
		},
		getStarRank() {
			// 
			this.$app.request(this.$app.API.STAR_RANK, {
				type: 1
			}, res => {
				this.steal_num = res.data.steal_num
				this.sprite_level = res.data.sprite_level
				this.steal_num_max = res.data.steal_num_max
				this.steal_count = res.data.steal_count
				this.steal_times = res.data.steal_times
				this.steal_times_max = res.data.steal_times_max
				// steal
				if (!this.$app.timeId) this.$app.timeId = []
				for (let v of this.$app.timeId) {
					clearInterval(v)
				}
				this.$app.timeId = []
				const resList = []
				let index = 0
				res.data.list.forEach((e, i) => {
					if (i < 6 && e.star.id != this.$app.getData('userStar')['id']) {
						resList.push({
							starid: e.star.id,
							name: e.star.name,
							steal_count: res.data.steal_count,
							avatar: e.star.head_img_s ? e.star.head_img_s : e.star.head_img_l,
							hot: this.$app.formatNumberRgx(e['week_hot']),
							steal: res.data.steal[index]
						})
						if (res.data.steal[index] > 0) {
							this.startStealTimeInterval(index)
						}
						index++
					}
				})

				this.starRankList = resList
			})
		},
		getUserRank(field = 'thisweek_count') {
			if (this.userRankPage > 10) return
			this.$app.request(this.$app.API.USER_RANK, {
				starid: this.star.id,
				page: this.userRankPage || 1,
				field,
			}, res => {
				const resList = []
				res.data.list.forEach((e, i) => {
					resList.push({
						user_id: e.user_id,
						avatar: e.user && e.user.avatarurl || this.$app.AVATAR,
						nickname: e.user && e.user.nickname || this.$app.NICKNAME,
						hot: e[field],
						like_count: e.like_count || 0,
					})
				})

				this.myRank = res.data.my
				if (this.userRankPage == 1) {
					this.userRankList[field] = resList
				} else {
					this.userRankList[field] = this.userRankList[field].concat(resList)
				}


			})
		},
		// 
		sendHotDanmaku(data) {
			if (!this.danmakuClosed) {
				this.sendDanmaku[this.sendDanmakuIndex].push(data)
				setTimeout(() => {
					// 12s
					this.sendDanmaku[this.sendDanmakuIndex].shift()
				}, 12000)
				if (++this.sendDanmakuIndex > 2) this.sendDanmakuIndex = 0
			}
		},

		getChartList() {
			this.$app.request(this.$app.API.STAR_CHART, {
				starid: this.star.id,
			}, res => {
				const resList = []
				res.data.forEach((e, i) => {
					const item = {
						uid: e.user && e.user.id,
						avatar: e.user && e.user.avatarurl || this.$app.AVATAR,
						nickname: e.user && e.user.nickname || this.$app.NICKNAME,
						content: e.content,
						// 
						captain: e.user && e.user.user_star && e.user.user_star.captain || 0,
						// 
						leader: e.user && e.user.isLeader,
						sendtimeInt: this.$app.strToTime(e.create_time),
					}

					resList.push(item)
				})

				this.chartList = resList
				this.$nextTick(function() {
					this.chartIndex = this.chartList.length - 1
				})

				this.$app.closeLoading(this)
			})
		},
		/**
		 * 
		 */
		joinGroup() {
			const clientId = this.$app.getData('clientId')
			if (!clientId) {
				this.$app.invokeSocket()
				setTimeout(() => {
					this.joinGroup()
				}, 1500)
			} else {
				this.$app.request(this.$app.API.STAR_JOINCHART, {
					star_id: this.star.id,
					client_id: clientId,
				}, res => {
					this.$app.closeLoading(this)
				})
			}
		},
		leaveGroup() {
			const clientId = this.$app.getData('clientId')
			if (clientId) {
				this.$app.request(this.$app.API.STAR_LEAVECHART, {
					star_id: this.star.id,
					client_id: clientId,
				}, res => {})
			}
		}
	}

}
